import React from 'react';
import './App.css';
import {
    Switch,
    Route,
    Redirect,
} from 'react-router-dom';
import profile, { getWords } from './profile';

function App() {
    return <div className="App">
        <MyWord />
    </div>
}

function MyWord() {
    return <Switch>
        <Route path="/home">
            <Home />
        </Route>
        <Route path="*">
            <Redirect to="/home" />
        </Route>
    </Switch>;
}

function Glass({ children, auto, height, oneLine }) {
    const _height = { height: height + 'px' };
    const classNames = ['glass', auto && 'auto', oneLine && 'one-line'].filter(Boolean).join(' ');

    return <div style={{ ...(height ? _height : {} ) }} className={classNames}>
        {children}
    </div>
}

function Logo({ text = 'My word' }) {
    return <h1 id="logo">{text}</h1>;
}

