function Text({ text, left, right, bg, link, href }) {
    const classNames = ['overflow-text', left && 'on-left', right && 'on-right', bg && 'bg'].filter(Boolean).join(' ');
    return <div className={classNames}>
        {link
            && <a target="_blank" rel="noopener noreferrer" href={href}><Sentence words={text} /></a>}
        {!link
            && <Sentence words={text} />}
    </div>
}

function Sentence({ words = getWords().slice(0, 5).join(' ') }) {
    return words.split(' ').map((x, key) => <span className="word" key={key}>{x}</span>);
}

function UserInput() {
    const tmp = React.useRef(createTmp());
    const inputRef = React.useRef(null);

    React.useEffect(() => {
        document.body.appendChild(tmp.current);
    }, []);

    const [value, setX] = React.useState('');
    const setValue = (e) => {
        const x = e.target.value;
        if (x.length < 25) {
            tmp.current.innerHTML = x;
            setTimeout(() => {
                const spaces = x.split(x, ' ').length;
                inputRef.current.style.width = tmp.current.clientWidth + spaces + 24 + 'px'
            });
            setX(x);
        }
    };

    return <input
        ref={inputRef}
        id="user-input"
        type="text"
        value={value}
        autoComplete="off"
        autoFocus
        onInput={setValue}
        placeholder=" " />
}

function createTmp() {
    const tmp = document.createElement('span');
    tmp.setAttribute('class', 'word');
    tmp.setAttribute('style', 'position: fixed; top: -1000px; visiblity: hidden; padding: 0 4px;');
    return tmp;
}

function format() {
    return new Date().toLocaleDateString('hu-HU', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour12: false,
        hour: '2-digit',
        minute: '2-digit'
    })
}

function Flex({ children, center, spaceBetween }) {
    const _center = { alignItems: 'center' };
    const _spaceBetween = { justifyContent: 'space-between' };
    return <div style={{ display: 'flex', ...(center ? _center : {}), ...(spaceBetween ? _spaceBetween : {}) }}>{children}</div>;
}

export default App;
